@import url(https://fonts.googleapis.com/css2?family=Indie+Flower&family=Special+Elite&display=swap);
.App{
  font-family: serif;
}

.backGround{
    height:100vh;
    width:100%;
    display: flex;
    /* flex-direction: column; */
    align-items: center;
    justify-content: center;
  }

  .motivvCard{
      border-width: 2px;
      align-items: center;
      margin: 20px;
      justify-content: center;
      height: 100%;
      width: -webkit-fit-content;
      width: -moz-fit-content;
      width: fit-content;
      padding: 2px;
  }

  .mobile{
      height: -webkit-fit-content;
      height: -moz-fit-content;
      height: fit-content;
      flex-direction: column;
      padding: auto;
      /* background-color: aqua; */
  }

  .overFlowScroll{
      overflow: scroll;
  }

  .headerHighlight{
      color: #d500f9;
  }

  .borderHighlight{
    border-color: #d500f9;
}

.primaryBackgrdColor{
    background-color: #d500f9;
}

/* mainevent will have a fixed width reponsive to window size and variable height to match poster resolution */
.mainEvent{
    height: min(90%,800px);
    padding: auto;
    /* background-color: yellow; */
    width: clamp(20%, 600px, 90%); 
}

.mainEvent.mobile{
    height: 600px;
    /* background-color: yellow; */
    padding: auto;
    width: clamp(70%,550px, 95%); 
}

.quickInfo{
    height: 350px;
    width: 90%;
}

.quickInfo.mobile{
    padding: auto;
    height: 350px;
    width: clamp(60%, 300px, 90%)
}

.desktop{
    padding: auto;
    /* background-color: aqua; */
  }

.quickInfoContainer{
    display: flex;
    flex-direction: column;
    width: clamp(20%, 600px, 40%);
    align-items: center;
    justify-content: center;
}

.homeMoreInfoBtnStyle{
    font-size: 24px;
    width: clamp(200px, 10vw, 200px);
    padding-left: 0.5rem;
    padding-right: 0.5rem;
    margin-bottom: 1vh;
}

.viewInfoBtn{
    font-size: 20px;
    width: 200px;
    padding-left: 0.5rem;
    padding-right: 0.5rem;
    border-color: white;
}

.getStartedBtnStyle{
    font-size: 20px;
    width: 200px;
    padding-left: 0.5rem;
    padding-right: 0.5rem;
}

.badge-style{
    height: 220px;
    max-width: 100%;
}

.customListWrapper{
    width: 10rem;
    margin: 0 auto;
}

.maxCardWidth{
    width: clamp();
}

ol.cnt{
    list-style: none;
    counter-reset: start 3;
}
  
  
ol.cnt > li:before{
    counter-increment: start;
    content: counter(start) ". ";
}



:root{
    --primary: #d500f9;
    --secondary: white;
}

.routeBtnContainer{
    max-height: 50px;
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
}

.btn{
    font-family: serif;
    border-radius: 5px;

}

.btn--animate{
    cursor: pointer;
    transition: -webkit-transform 0.3s ease;
    transition: transform 0.3s ease;
    transition: transform 0.3s ease, -webkit-transform 0.3s ease;
}
.btn--animate:hover{
    -webkit-transform: translateY(-5px);
            transform: translateY(-5px);
}

.btn--solid {
    background-color: #d500f9;
    background-color: var(--primary);
    color: white;
    border-width: 2px;
    border-color: #d500f9;
    border-color: var(--primary);
}

.btn--solid:hover{
    color: #d500f9;
    color: var(--primary);
    background-color: white;
    border-width: 2px;
    border-color: #d500f9;
    border-color: var(--primary);
}

.btn--outline{
    color: #d500f9;
    color: var(--primary);
    background-color: white;
    border-width: 2px;
    border-color: #d500f9;
    border-color: var(--primary);
}

.btn--outline:hover{
    background-color: #d500f9;
    background-color: var(--primary);
    color: white;
    border-width: 2px;
    border-color: #d500f9;
    border-color: var(--primary);
}
.bgHeaderHeight{
    max-height: 555px;
}

.featuredEventPoster{
    max-width: 350px;
    max-height: 555px;
}

.featuredEventInfo{
    max-height: 250px;
    max-width: 500px;
}

.eventsList{
    display: flex;
    flex-wrap: wrap;
    margin: -1rem -1rem;
    padding-bottom: 3rem;
}

